.catalog {
  margin: 0 auto;

  width: 1110px;
  padding-left: 145px; // как у кнопки в Header
  padding-right: 145px;

  background-color: rgba($pink, .15);
}

.catalog__tort {
  position: absolute;
  display: block;
  top: 1035px;
  right: 160px;
}

.catalog__title {
  text-align: left;
  padding-top: 3px;
}

.catalog__nav {
  text-align: left;

  a {
    color: black;
    font-size: 20px;
    text-decoration: none;
    margin-right: 1.4rem;

    &:hover {
      color: orange;
      border-bottom: 1px solid orange;
    }
  }
}

.catalog__items {
  display: flex;
  flex-wrap: wrap;

  margin-top: 35px;
}

.cat__item {
  width: 31.33333%;
  padding: 10px;
  margin-right: 2%;
  margin-bottom: 2%;
  z-index: 10;

  background-color: $white;
  border: 1px solid $orange;
  border-radius: 10px;

  box-shadow: 0 0 5px rgba($black, .25);

    transition: box-shadow .2s linear;

    &:hover {
        box-shadow: 0 0 15px rgba($black, .25);
    }

    img {
      width: 235px;
      height: 176px;
      object-fit: cover;
    }

    .item__title {
      font-size: 17px;
      font-weight: bold;
      padding-top: 5px;
    }

    .item__desc {
      font-size: 14px;
      padding-top: 5px;
      padding-bottom: 10px;
    }

    .item__price {
      text-align: center;
      padding: 5px 0;

      font-size: 14px;
      font-weight: bold;
      color: $white;

      border: 1px solid $orange;
      border-radius: 10px;
      background-color: $orange;
    }
}
